@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@1,100;1,300&family=Lato&display=swap);
html, body {
  width:100%;
  height: 100%;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Poppins",-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root, body{
  width: 100%;
  height: 100%;
  background-color: #282c34;
}

.error_class {
  font-size: .85em;
  color: #ffc31b;
  padding: .4em;
  margin: auto;
  margin-bottom: 2px;
  border-radius: 7px;
  border: solid 2px #ffc31b;
  background: #06429e;
  text-align: center;
}



.post_container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 2em 3em;
  border-radius: 12px;
}

.topic_post {
  border-radius: 3px;
  border: 2px solid black;
  text-align: center;
  border-radius: 12px;
  padding: 5px;
}

.post_image img {
  border-radius: 16px;
  margin:2px;
}

.post_username {
 padding: 6px;
 color: gray;
}

.post_content {
  padding: 5px 12px;
  margin-bottom: auto;
}

.post_button_div {
  margin: 2px;
}

.post_button {
  margin: 2px;
}

.post_time {
  font-size: 11px;
  color: gray;
  padding: 14px 18px;
  margin-left: auto;
}

.add_post_form_container {
  background-color: rgb(97, 156, 207);
  margin: 12px 20px;
  border-radius: 20px;
}

.add_post_form_container * {
padding: 12px;
}

.grid {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: stretch;
          align-items: stretch;
}

.card {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.card-header,
.card-content {
  padding: 1rem;
}

.card-header h1,
.card-content h1,
.card-content h2,
.card-content p {
  margin: 0;
}

.card-image {
  width: 100%;
}

.card-image img {
  width: 100%;
}
.card-image img:hover {
  opacity: .8;
}

.card-actions {
    padding: 1rem;
    text-align: center;
}

.card-actions button,
.card-actions a {
    margin: 0 0.25rem;
}

.card-actions button {
  font: inherit;
  border: 1px solid #00695c;
  color: #00695c;
  background: white;
  border-radius: 3px;
  cursor: pointer;
}

.margined {
  margin: 5px 36px;
}

.card-actions button:hover,
.card-actions button:active {
  background-color: #00695c;
  color: white;
}

form .invalid {
  border-color:red;
}

.pagination {
  margin:  auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.pagination button{
  margin-top: 0 1rem;
  text-decoration: none;
  color:#203f6b;
  padding: 0.5rem;
  margin: 0 1rem;
  background-color: white;
  padding: 1rem;
  border-radius: 12px;
  font-weight: 700;
  border: 2px solid blue;
}

.pagination button:hover, button:active, button:focus {
  background-color:#06429e;
  color:white;
}


.img-post {
  max-height: 500px;
  width:auto;
}

@media only screen and (max-width: 780px) {
  .img-post {
    max-height: 400px;
  }
}

.choose_file {
  margin:2px;
}

.round {
    border-radius: 2.25rem!important;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    font-family: 'Lato', sans-serif;
  }
  
  .wrapper{
    width: calc(60% + 10em);
    margin: 1em 18em; 
    display: -webkit-flex; 
    display: flex;
    box-shadow: 0 1px 20px 0 rgba(69,90,100,.08);
  }

  .profile_page_wrapper {
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: center;
              justify-content: center;
  }
  
  .wrapper .left{
    width: 45%;
    background: linear-gradient(to right,#502dbb,#01dbdf);
    padding: 30px 25px;
    border-top-left-radius: 40px;
    border-bottom-left-radius: 40px;
    text-align: center;
    color: #fff;
  }
  
  .wrapper .left img{
    border-radius: 5px;
    margin-bottom: 10px;
    margin-top: 85px;
  }
  
  .wrapper .left h4{
    margin-bottom: 10px;
  }
  
  .wrapper .left p{
    font-size: 12px;
  }
  
  .wrapper .right{
    width: 65%;
    background: #fff;
    padding: 30px 25px;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
  }
  
  .wrapper .right .info,
  .wrapper .right .account{
    margin-bottom: 25px;
  }
  
  .wrapper .right .info h3,
  .wrapper .right .account h3{
      margin-bottom: 15px;
      padding-bottom: 5px;
      border-bottom: 1px solid #e0e0e0;
      color: #353c4e;
    text-transform: uppercase;
    letter-spacing: 5px;
  }
  
  .wrapper .right .info_data,
  .wrapper .right .account_data{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  
  .wrapper .right .info_data .data,
  .wrapper .right .account_data .data{
    width: 45%;
  }
  
  .wrapper .right .info_data .data h4,
  .wrapper .right .account_data .data h4{
      color: #353c4e;
      margin-bottom: 5px;
  }
  
  .wrapper .right .info_data .data p,
  .wrapper .right .account_data .data p{
    font-size: 13px;
    margin-bottom: 10px;
    color: #919aa3;
  }

.profile-form-container {
    padding:1px;
    padding: 25px;
    background: white;
    border-radius: 30px;
    margin-top: 12px;
}

.profile-form-label {
    padding: 1px;
    font-weight: 700;
    font-style: italic;
}

.fancy-hr {
    padding: 1px;
    color:#282c34;    
}

@media  only screen and (max-width: 400px) {
  img {
    width:85px;
  }
   h3 {
    font-size: 1.3rem;
  }
  p {
    font-size: 10px;
  }
  .wrapper right {
    width: 55%;
  }
}

